.content {
    position: relative;
    width: 100vw;
    height: 100vh;
    font-family: "LabGrotesqueRegular";
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
}
.content.active {
    background: rgba(0, 0, 0, 0.3);
}
.tax-deduction-btn {
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 6px;
    color: #FFFFFF;
    padding: 16px 32px;
    font-size: 16px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: 0.5s;
}

.tax-deduction-btn:hover, .tax-deduction-btn:focus {
    background: #FFFFFF;
    color: #000000;
}

button {
    background: transparent;
    border: none;
    padding: 0;
}
