@font-face {
    font-family: "LabGrotesqueRegular";
    src: url(/outside-digital/static/media/LabGrotesqueRegular.a25b6e0c.eot);
    src: url(/outside-digital/static/media/LabGrotesqueRegular.a25b6e0c.eot?#iefix)format("embedded-opentype"),
    url(/outside-digital/static/media/LabGrotesqueRegular.446ca450.woff) format("woff"),
    url(/outside-digital/static/media/LabGrotesqueRegular.2b7d5ea4.ttf) format("truetype");
    font-style: normal;
    font-weight: 400;
}
.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 1;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal__content {
    padding: 32px;
    border-radius: 12px;
    background-color: white;
    width: 100%;
    max-width: 552px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    transition: 0.4s all;
    box-sizing: border-box;
    position: relative;
}

.modal__content.active {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.modal__head {
    color: #000000;
    margin-bottom: 16px;
    font-size: 28px;
    line-height: 40px;
    font-weight: bold;
}

.modal__paragraph {
    font-size: 14px;
    line-height: 24px;
    color: #808080;
    margin-bottom: 24px;
}

.modal__label {
    font-size: 14px;
    color: #000000;
    margin-bottom: 8px;
    display: inline-block;
    font-weight: bold;
}

.modal__input {
    width: 100%;
    display: block;
    border: 1px solid #DFE3E6;
    box-sizing: border-box;
    border-radius: 3px;
    margin-bottom: 8px;
    padding: 8px 10px;
}

.modal-calculation__btn {
    font-size: 14px;
    line-height: 24px;
    color: #EA0029;
    font-weight: 600;
}

.modal-calculation__btn:hover {
    color: #F53A31;
}

.modal-calculation__btn:focus {
    color: #EA0029;
}

.modal-quad-block__text {
    margin-bottom: 8px;
    display: inline-block;
    font-weight: bold;
    margin-top: 16px;
}

.modal-quad-block__money {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 16px 0;
    border-bottom: 1px solid #DFE3E6;
}


.modal-quad-block__money:last-child {
    margin-bottom: 28px;
}

.modal-decrease {
    display: flex;
    align-items: center;
}

.modal-decrease__clarification {
    margin-right: 32px;
}

.modal-decrease__payment-btn {
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    border-radius: 50px;
    color: #FFFFFF;
    margin-right: 16px;
    padding: 12px;
    transition: 0.5s;
}

.modal-decrease__payment-btn:hover, .modal-decrease__payment-btn:focus {
    background: #EA0029;
}

.modal-decrease__term-btn {
    background: #EEF0F2;
    border-radius: 50px;
    color: #000000;
    padding: 12px;
    transition: 0.5s;
}

.modal-decrease__term-btn:hover, .modal-decrease__term-btn:focus {
    background: #BEC5CC;
}

.modal-add {
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    box-shadow: 0 0 24px rgba(234, 0, 41, 0.33);
    border-radius: 6px;
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 40px;
    padding: 16px 0;
    color: #FFFFFF;
    transition: 0.5s;
}

.modal-add:hover, .modal-add:focus {
    background: #EA0029;
}

.modal_error {
    color: #EA0029;
    font-weight: bold;
}

.close {
    position: absolute;
    right: 27px;
    top: 27px;
    width: 18px;
    height: 18px;
    opacity: 1;
    cursor: pointer;
}

.close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 18px;
    width: 2px;
    background-color: #EA0029;
}
.close:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.close:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.custom-checkbox+label {
    display: inline-flex;
    align-items: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.custom-checkbox+label::before {
    content: '';
    display: inline-block;
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 0.25em;
    margin-right: 0.5em;
    margin-top: -0.2em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.custom-checkbox:checked+label::before {
    border-color: #FF5E56;
    background-color: #FF5E56;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

@media (max-width: 480px) {
    .modal {
        padding: 25px;
    }
    .modal-decrease {
        flex-wrap: wrap;
    }
    .modal-decrease__clarification {
        width: 100%;
        margin: 0 0 15px 0;
    }
}

